import React from "react";

export default function JobList({ joblists, searchString }) {
  return (
    <>
      {joblists
        .filter((joblist) => {
          if (searchString === "") {
            return joblist;
          } else if (
            joblist.role.toLowerCase().includes(searchString.toLowerCase()) ||
            joblist.languages
              .toString()
              .toLowerCase()
              .includes(searchString.toLowerCase())
          ) {
            return joblist;
          }
        })
        .map((joblist) => {
          return (
            <div
              className={joblist.featured ? "job-list featured" : "job-list"}
              key={joblist.id}
            >
              <div className="avatar">
                <img src={joblist.logo} alt="photosnap" />
              </div>
              <div className="job-description">
                <div className="job-head">
                  <h5>{joblist.company}</h5>
                  {joblist.new ? <span className="new">New!</span> : ""}
                  {joblist.featured ? (
                    <span className="feature">Featured</span>
                  ) : (
                    ""
                  )}
                </div>
                <div className="job-title">
                  <h4>{joblist.position}</h4>
                  <ul className="job-location">
                    <li>{joblist.postedAt}</li>
                    <li>{joblist.contract}</li>
                    <li>{joblist.location}</li>
                  </ul>
                </div>
              </div>
              <div className="job-tag">
                <div className="tag-list">
                  <span>{joblist.role}</span>
                  {joblist.languages.map((language) => {
                    return <span key={language}>{language}</span>;
                  })}
                </div>
              </div>
            </div>
          );
        })}
    </>
  );
}
