import { React, useState } from "react";
import reactDom from "react-dom";
import "./css/style.css";
import JobList from "./JobList";

function DisplayJobList() {
  let jobLists = require("./data.json");
  const [inputValue, setInputValue] = useState("");

  return (
    <>
      <header>
        <div className="banner"></div>
        <div className="search">
          <div className="text-input">
            <input
              onChange={(e) => {
                setInputValue(e.target.value);
              }}
              value={inputValue}
              type="text"
            />
          </div>
          <span
            className="clear"
            onClick={() => {
              setInputValue("");
            }}
          >
            Clear
          </span>
        </div>
      </header>
      <section className="job-lists">
        <JobList joblists={jobLists} searchString={inputValue} />
      </section>
    </>
  );
}

reactDom.render(<DisplayJobList />, document.getElementById("root"));
